// Import Swiper styles
import 'swiper/css';
import 'swiper/css/effect-fade';
import 'swiper/css/pagination';

import Swiper from 'swiper';
import { Pagination, Autoplay, EffectFade, Keyboard, Navigation } from 'swiper/modules';

document.addEventListener('DOMContentLoaded', () => {
    const swiperEl = document.querySelector('.swiper');

    if (!swiperEl) {
        console.warn('Swiper element not found');
        return;
    }

    const slideEls = Array.from(swiperEl.querySelectorAll('.swiper-slide'));
    const titles = slideEls.map((el, i) => el.getAttribute('data-title') || `Slide ${i + 1}`);
    const urls = slideEls.map((el) => el.getAttribute('data-url') || '#');
    const ctaEl = document.querySelector('.js-swiper-cta');

    const swiper = new Swiper(swiperEl, {
        modules: [Pagination, Autoplay, EffectFade, Keyboard, Navigation],
        effect: 'fade',
        fadeEffect: {
            crossFade: true
        },
        loop: true,
        speed: 700,
        autoplay: {
            delay: 5000,
            disableOnInteraction: false,
        },
        allowTouchMove: false,
        pagination: {
            el: '.swiper-pagination',
            type: 'custom',
            renderCustom: (swiper, current, total) => {
                const idx = (current - 1) % titles.length;
                const title = titles[idx] || `Slide ${current}`;
                const url = urls && urls[idx] ? `href="${urls[idx]}"` : '';
                return `<a ${url} class="swiper-pagination-title">${title}</a>`;
            }
        },
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },
        keyboard: {
            enabled: true,
            onlyInViewport: false,
        },
    });

    const updateCta = () => {
        if (!ctaEl) return;
        const idx = (swiper.realIndex ?? 0) % urls.length;
        const url = urls[idx] || '#';
        ctaEl.setAttribute('href', url);
    };

    updateCta();
    swiper.on('slideChange', updateCta);
});