const app = (() => {
	let body;
	let menu;
	let menuItems;
	let ham;
	let nav;
	let lastScroll = 0;

	const init = () => {
		body = document.querySelector('body');
		menu = document.querySelector('.menu-icon');
		menuItems = document.querySelectorAll('.nav__list-item');
		ham = document.querySelector('.ham');
		nav = document.querySelector('.site-header');

		applyListeners();
		initScroll();
	}

	const applyListeners = () => {
		menu.addEventListener('click', (e) => {
			toggleNav();
			if (ham) {
				ham.classList.toggle('active');
			}
		});

		menuItems.forEach(item => {
			item.addEventListener('click', () => {
				body.classList.remove('nav-active');
				body.style.overflow = '';
				if (ham) {
					ham.classList.remove('active');
				}
			});
		});
	}

	const toggleNav = () => {
		body.classList.toggle('nav-active');

		if (body.classList.contains('nav-active')) {
			body.style.overflow = 'hidden';
		} else {
			body.style.overflow = '';
		}
	}

	const initScroll = () => {
		handleScroll();

		window.addEventListener('scroll', handleScroll);
	};

	const handleScroll = () => {
		if (!nav) return;

		const currentScroll = window.pageYOffset;

		if (currentScroll > 10) {
			nav.classList.add('nav--scrolled');
		} else {
			nav.classList.remove('nav--scrolled');
		}

		if (currentScroll > lastScroll && currentScroll > 100) {
			nav.classList.add('nav--scroll-down');
			nav.classList.remove('nav--scroll-up');
		} else if (currentScroll < lastScroll) {
			nav.classList.remove('nav--scroll-down');
			nav.classList.add('nav--scroll-up');
		}

		lastScroll = currentScroll;
	};

	init();
})();
